<template>
  <div>
    <el-button size="mini" type="primary" @click="formatJson"
      >格式化数据</el-button
    >
    <b-code-editor ref="codeEditor" v-model="jsonData"></b-code-editor>
    <el-button @click="getValue" size="mini" type="success">生成</el-button>
    <div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="date" label="日期" width="150">
        </el-table-column>
        <el-table-column label="配送信息">
          <el-table-column prop="name" label="姓名" width="120">
          </el-table-column>
          <el-table-column label="地址">
            <el-table-column prop="province" label="省份" width="120">
            </el-table-column>
            <el-table-column prop="city" label="市区" width="120">
            </el-table-column>
            <el-table-column prop="address" label="地址" width="300">
            </el-table-column>
            <el-table-column prop="zip" label="邮编" width="120">
            </el-table-column>
          </el-table-column>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      jsonData: JSON.stringify({}),
      tableData: []
    }
  },
  methods: {
    formatJson() {
      this.$refs.codeEditor.formatCode()
    },
    getValue() {
      const value = this.$refs.codeEditor.getValue()
      const { data } = JSON.parse(value)
      this.tableData = data
    }
  }
}
</script>

